export default (preState = {
    user_id:0,
    username:'未登录',
    user_img:'data:image/jpeg;base64,',
    user_like:''
}, action) => {
    const { type, data } = action
    switch (type) {
        case "current_userinfo":
            
            return data;
        default:
            return preState
    }

}